@font-face {
  font-family: Flower;
  src: url('./fonts/FlowerLeafDemo-1GqlB.ttf') format('truetype');
}
*{
  margin:0;
  padding:0;
  color: #242424;
  font-family: 'Roboto', sans-serif;
  word-wrap: break-word;
  overflow-wrap: break-word;
  overflow-x: hidden;
}

h1,h2,h3,h4,h5,h6{
  font-weight: 900;
  color: rgb(24, 24, 24);
}

.div-global{
  width: 100%;
  margin: 0 auto;
}

main{
  display: block;
  background-color: #FFF;
}

.container{
  width: 100%;
  max-width: 760px;
  margin: 0 auto;
  padding: 0 20px;
}

section.module p{
  margin-bottom: 40px;
}

section.module:last-child{
  margin-bottom: 0;
}

section.module h2{
  margin-bottom: 40px;
  font-size: 30px;
}

section.module.content{
 padding: 40px 0;
 background: #c2c0b9 !important; 
}

section.module.parallax{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh !important;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
}

section.module.parallax:after{
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 8;
  background: -moz-linear-gradient(to bottom, rgba(0,0,0,0) 0, rgba(0,0,0,0) 40%, #000 100% );
  background: -webkit-linear-gradient(to bottom, rgba(0,0,0,0) 0, rgba(0,0,0,0) 40%, #000 100% );
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0, rgba(0,0,0,0) 40%, #000 100% );
}

section.module.parallax-1{
  background-image: url('./img/Background\ 1.jpg');
}

section.module.parallax-2{
  background-image: url("./img/Background\ 2.jpg");
}

section.module.parallax-3{
  background-image: url("./img/Background\ 3.jpg");
}

section.module.parallax h1{
  color: rgb(255, 255, 255);
  text-shadow: 4px 4px 4px black;
  font-family: Flower;
  text-align: center;
  z-index: 50;
  font-size: 5vw;
  padding: 5%;
}

.cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 30px;
  max-width: 960px;
  margin: 0 auto;
  padding-bottom: 3%;
}

article {
  position: relative;
}

.article-img {
  height: 300px;
  width: 100%;
  object-fit: cover;
}

.article-img:hover {
  transform: scale(1.1);
  transition: .3s ease-in-out;
  box-shadow: 5px 5px 10px black;
}

footer {
  height: auto;
  background-color: #242424;
  color: #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.footerDivTexto {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.footerDivTexto h1 {
  color: #FFF;
  padding-top: 15%;
}

.footerDivTexto p {
  color: #FFF;
  padding-top: 5%;
}

.footerDivTexto img {
  height: 35vh;
}

.footerSpan {
  background-color: #000;
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footerSpan span { 
  color: #FFF;
}

.footerSpan strong {
  color: #FFF;
  text-decoration: none;
}

@media all and (max-width: 820px) {
  section.module h2 {
    font-size: 32px;
  }
  .module h2, p{
    padding: 0 8px;
  }

  .container{
    margin:0;
    padding:0;
  }
  .container h3 {
    padding-left: 3%;
  }
  section.module.parallax h1 {
    font-size: 3vw;
  }

  section.module.parallax h1 {
    font-size: 36px;
  }
}
